export const sortByTime = (chats = [], lastMessages = {}) => {

    return chats.slice().sort((a, b) => {
        const { response: resA, id: idA } = a;
        const { response: resB, id: idB } = b;
        const { at: atA } = lastMessages[idA] || { at: a.createdAt };
        const { at: atB } = lastMessages[idB] || { at: b.createdAt };
        const aEnded = resA === 'ended';
        const bEnded = resB === 'ended';
        const bothEnded = resA === 'ended' && resB === 'ended';
        const bothStarted = resA === 'accepted' && resB === 'accepted';

        if(bothEnded || bothStarted) return +new Date(atB) - +new Date(atA);
        if(aEnded) return 1;
        if(bEnded) return -1;
        return +new Date(atB) - +new Date(atA);
    });
};