import socketIOClient from 'socket.io-client';

let BASE = "http://localhost:5000";
if(process.env.NODE_ENV !== 'development'){
    BASE = "https://tradingapi-dot-mimimo-react.appspot.com";
};

export default class Sock{

    constructor(){
        this.socketRef = socketIOClient(BASE);
    };

    disconnect = () => this.socketRef.disconnect();
    emitEvent = (name, data) => this.socketRef.emit(name, data);
    listenEvent = (name, callback) => this.socketRef.on(name, callback);
};
