import React from 'react';
import { MdArrowDropDown } from 'react-icons/md';
import { connect } from 'react-redux';
import { INPUT_DARK_STYLES } from '../../styles';

import s from './Input.module.scss';

const TextInput = ({
    value = '',
    onChange = () => {},
    placeholder = 'Search...',
    type = 'text',
    containerClassName = '',
    inputClassName = '',
    uid = '',
    required = true,
    themeReducer: tR,
    children,
    onBlur, onFocus,
    readOnly = false
}) => {

    return(
        <div style={tR === 'dark' ? {
                boxShadow: 'none'
            } : {}} className = {[s.textinput, containerClassName].join(' ')}>
            {(uid && value) ? <label className = {s.textinput_label} htmlFor = {uid}>
                {placeholder}
            </label> : <></>}
            <input 
                onBlur={onBlur}
                style = {tR === 'dark' ? INPUT_DARK_STYLES : {}}
                name = {uid}
                id = {uid} readOnly={readOnly}
                value = {value}
                onChange = {e => onChange(e.target.value)}
                className = {[s.textinput_input, inputClassName, readOnly ? s.readOnly : ''].join(' ')}
                type = {type} onFocus={onFocus}
                placeholder = {!readOnly ? placeholder : ''}
                required = {required}
            />
            {children}
        </div>
    )

};

const matchStateToProps = (state) => {
    const { themeReducer } = state;
    return { themeReducer };
};

export default connect(matchStateToProps)(TextInput);



const defaultOptions = ['Yes', 'No'];

export const DropDown = ({
    options = defaultOptions,
    selected  = '',
    onChooseOption = () => {},
    showDropdown = true,
    required = true,
    uid,
    placeholder,
    inputClassName = '',
    containerClassName = '',
    onRequestDropdown = () => {}
}) => {

    return (

        <div className = {[s.textinput, containerClassName].join(' ')}>
            {uid && <label className = {s.textinput_label} htmlFor = {uid}>{placeholder}</label>}
            <input 
                name = {uid}
                id = {uid}
                value = {selected}
                readOnly
                onChange = {onChooseOption}
                className = {[s.textinput_input, inputClassName].join(' ')}
                placeholder = {placeholder}
                required = {required}
            />
            
            {showDropdown && <div className = {s.dropdown}>
                {options.map(el => <span 
                    onClick = {() => onChooseOption(el)} 
                    key = {el} 
                    className = {s.dropdown_element}
                >
                    {el}
                </span>)}
            </div>}
            
            <div onClick = {onRequestDropdown} className = {s.arrow}>
                <MdArrowDropDown />
            </div>

        </div>
    )
};