import React, { useState, useEffect } from 'react';
import { 
    useLocationState, useMessage, 
    useSocket, useTyping, useTypingEmitter 
} from '../../../hooks';
import get from 'lodash/get';
import s from './Chat.module.scss';
import PrimaryHeading from '../../../widgets/Heading/Heading';
import Window from '../../../widgets/Window/Window';
import { connect } from 'react-redux';
import { reviewChat } from '../../../actions';
import Warning, { useWarning } from '../../../widgets/Warning/Warning';

const Chat = ({
    dispatch
}) => {
    const newSock = useSocket();
    const [mess, setMess] = useWarning('');
    const [data] = useLocationState('data', '/query', s => get(s, 'chat'));
    const [allMessages, isAlive, canReview] = useMessage(get(data, 'chat.id')); 
    const [message, setMessage] = useState('');
    const typingStatus = useTyping(message, 'user', get(data, 'chat.id'));
    const [done, setDone] = useState(false);
    useTypingEmitter(newSock, message, 'user', get(data, 'chat.id'));

    useEffect(() => {
        if(data && newSock){
            newSock.emitEvent('SEARCH_EXECUTIVES', { id: null });
            newSock.emitEvent('SET_USER_SOCKET', { chatId: get(data, 'chat.id') });
        }
    }, [data, newSock]);
    const sendMessage = (content, clientId) => {
        newSock.emitEvent('SEND_MESSAGE', { content, clientId, by: 'user', parentChat: get(data, 'chat.id'), createdAt: new Date()});
        newSock.emitEvent('LAST_MESSAGE', { parentChat: get(data, 'chat.id'), content, by: 'User', at: new Date(), highlight: true });
        setMessage('');
    };
    const onEnd = () => {
        newSock.emitEvent('END_CHAT', { chatId: get(data, 'chat.id') })
    };

    return <>
        <Warning text={mess} className={s.main_warning} />
        <Window 
            message={message} canEnd
            setMessage={setMessage}
            messages={allMessages} className={s.main_window}
            onSend={sendMessage} canReview={canReview && !done}
            onEnd={onEnd} isAlive={isAlive}
            onReview={i => {
                setDone(true);
                dispatch(reviewChat({ impact: i, chatId: get(data, 'chat.id') }));
            }} initiator='We are finding an executive for you, till then you can introduce your query.'
            canLoad={!allMessages.length} otherTyping={typingStatus.executive}
            header={`#${get(data, 'chat.referenceId', '')}`}
        />
    </>
};

export default connect()(Chat);