import ScrollToBottom from 'react-scroll-to-bottom';
import moment from 'moment';
import { useLocation } from 'react-router-dom';
import PrimaryButton from '../../../widgets/Button/Button';
import { LoadingDots } from '../../../widgets/Loader/Loader';
import s from './Notification.module.scss';
import get from 'lodash/get';
import { getFirstN } from '../../../hooks';
import { sortByTime } from '../../../utils';

export const NotificationsJSX = ({
    queries,
    user,
    excludeText,
    respondToQuery,
    type='other',
    lastMessages = {}
}) => {

    const anim = [s.beat, s.pulse].join(' ');
    const { hash } = useLocation();
    
    return <>
        <ScrollToBottom mode='top'>            {
                (lastMessages ? sortByTime(queries, lastMessages) : queries).map(q => <div 
                    className={[s.main_item, q.response === 'ended' ? s.ended : '', 
                    (hash !== `#${q.id}` && get(lastMessages, [q.id, 'highlight'], null)) ? anim : ''].join(' ')} 
                    key={q.id}
                >
                    <span className={s.main_item_text}>
                        <span className={s.main_item_text_title}>{q.title}</span>
                        {get(lastMessages, [q.id, 'by'], null) ?
                            <> 
                                <span className={s.main_item_text_time}>
                                    {lastMessages[q.id].by}: {getFirstN(lastMessages[q.id].content, 25)}
                                </span>
                                <span className={s.main_item_text_time}>{moment(lastMessages[q.id].at).fromNow()}</span>
                            </> : 
                        <></>}
                    </span>
                    {(hash !== `#${q.id}` || type !== 'my') ? <PrimaryButton 
                        clickable={excludeText.id !== q.id} className={s.main_item_button}
                        text={excludeText.id === q.id ? <LoadingDots /> : q.answeredBy === user.id ? 'Reopen' : 'Respond'}
                        onProceed={() => respondToQuery(q)}
                    /> : <></>}
                </div>)
            }
        </ScrollToBottom>
    </>
};
