import React, { useState, useEffect } from 'react';
import Card from '../../../widgets/Card/Card';
import PrimaryButton from '../../../widgets/Button/Button';
import PrimaryHeading from '../../../widgets/Heading/Heading';

import { useHistory } from 'react-router-dom';
import s from './Start.module.scss';
import Warning, { useWarning } from '../../../widgets/Warning/Warning';
import { connect } from 'react-redux';
import { LoadingDots } from '../../../widgets/Loader/Loader';
import { createChat } from '../../../actions';

const Start = ({
    dispatch,
    createChatReducer
}) => {

    const { push } = useHistory();
    const [warning, setWarning] = useWarning('');
    const [buttonText, setButtonText] = useState('Start Chat');
    const [chatTitle, setChatTitle] = useState('');
    const create = () => {
        if(!chatTitle) return setWarning('Please input an explaination for your query.');
        setButtonText(<LoadingDots />);
        dispatch(createChat({ chatTitle, createdAt: new Date() }));
    };
    useEffect(() => {
        if(!createChatReducer) return;
        setButtonText('Start Chat');
        const { success, message, ...data } = createChatReducer;
        if(!success) return setWarning(message);
        push({ pathname: '/chat', state: { data } });
        return () => dispatch({ type: 'CREATE_CHAT', payload: null });
    }, [createChatReducer])
    
    return <main className={s.main}>
        <Card className={[s.main_card, s.card].join(' ')}>
            <Warning text={warning} />
            <PrimaryHeading 
                text='Initiate Chat'
                className={s.main_card_heading}
            />
            <textarea 
                placeholder='Explain your query in no more than 75 words.'
                value={chatTitle} className={s.main_card_text}
                onChange={e => setChatTitle(e.target.value.substr(0, 75))}
            />
            <PrimaryButton 
                text={buttonText}
                className={s.main_card_button}
                onProceed={create}
            />
        </Card>
    </main>
};

const matchStateToProps = (state = {}) => {
    const { createChatReducer } = state;
    return { createChatReducer };
}
export default connect(matchStateToProps)(Start);