import React from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { HEADING_DARK_STYLES, LINK_HEADING_DARK_STYLES } from '../../styles';

import s from './Heading.module.scss';

const PrimaryHeading = ({
    text = '',
    capital = true,
    className = '',
    themeReducer: tr,
    onClick = () => {},
    ...props
}) => {

    const CAP = { textTransform: capital ? 'uppercase' : 'unset' };
    return (
        <h1 
            onClick={onClick}
            className = {[s.main, className].join(' ')}
            style={tr === 'dark' ? {...HEADING_DARK_STYLES, ...CAP} : {...CAP}}
        >
            {text || props.children}
        </h1>
    )
};

const matchStateToProps = (state) => {
    const { themeReducer } = state;
    return { themeReducer };
};


const LinkHeadingPlain = ({
    data = [],
    themeReducer: tr,
    ...props
}) => {

    return (
        <div className={[s.link, props.className].join(' ')}>
            {data.filter(Boolean).map((el, ind, arr) => {

                const { name, to } = el;
                return <NavLink 
                    key={name} 
                    className={[s.link_name, ind !== arr.length - 1 ? s.after : ''].join(' ')} 
                    to={to}
                    style={tr === 'dark' ? LINK_HEADING_DARK_STYLES : {}}
                >
                    {name}
                </NavLink>
            })}
        </div>
    )
}

export const LinkHeading = connect(matchStateToProps)(LinkHeadingPlain);
export default connect(matchStateToProps)(PrimaryHeading);