import React, { useEffect, useState } from 'react';
import { v4 } from 'uuid';
import Warning from '../../../widgets/Warning/Warning';
import PrimaryHeading from '../../../widgets/Heading/Heading';

import { useHistory } from 'react-router-dom';
import s from './Notification.module.scss';
import { useWarning } from '../../../widgets/Warning/Warning';
import { connect } from 'react-redux';
import { joinChat } from '../../../actions';
import { useLastMessage, useSocket } from '../../../hooks';
import { NotificationsJSX } from './Notifications';

const Notification = ({
    user, className, orient = 'row',
    dispatch, joinChatReducer, isLeft
}) => {
    const { push } = useHistory();
    const [warning, setWarning] = useWarning('');
    const [excludeText, setExcludeText] = useState({});
    const newSock = useSocket();
    const [queries, setQueries] = useState({ my: [], other: [] });
    const [lastMessages, setLastMessages] = useLastMessage(queries.my);
    
    useEffect(() => {
        if(!newSock) return;
        newSock.emitEvent('SEARCH_EXECUTIVES', { id: null, executiveId: null });
        newSock.emitEvent('SEARCH_EXECUTIVES', { id: null, executiveId: user.id });
        newSock.listenEvent('INCOMING_CHAT', chats => setQueries(q => ({ my: q.my, other: chats })));
        newSock.listenEvent(`${user.id}_INCOMING_CHAT_ME`, chats => setQueries(q => ({ my: chats, other: q.other })));
        return () => dispatch({ type: 'JOIN_CHAT', payload: null });
    }, [newSock]);
    
    useEffect(() => {
        if(!joinChatReducer || !newSock) return;
        const chat = { ...excludeText };
        setExcludeText({});
        const { message, success } = joinChatReducer;
        if(!success) return setWarning(message);
        newSock.emitEvent('SEND_MESSAGE', { 
            by: 'admin', clientId: v4(), parentChat: chat.id, createdAt: new Date(),
            content: `${user.username} have joined as customer care executive.`, 
        });
        newSock.emitEvent('SEND_MESSAGE', { 
            clientId: v4(), parentChat: chat.id, createdAt: new Date(),
            content: `This chat is regarding: ${chat.title}.`, by: 'admin' 
        });
        newSock.emitEvent('SEARCH_EXECUTIVES', { id: null, executiveId: null });
        newSock.emitEvent('SEARCH_EXECUTIVES', { id: null, executiveId: user.id });
        push({ pathname: '/executiveChat', hash: chat.id, state: { user, chat } });
        return;
    }, [joinChatReducer]);

    const respondToQuery = q => {
        setLastMessages(m => ({ ...m, [q.id]: { ...m[q.id], highlight: false } }));
        setExcludeText(q);
        if (queries.my.some(el => el.id === q.id)){
            push({ pathname: '/executiveChat', hash: q.id, state: { user, chat: q } });
            return;
        };
        dispatch(joinChat({ chatId: q.id, userId: user.id }));
    };
    const props = { 
        user, excludeText, respondToQuery, 
        setWarning, joinChatReducer,
        setExcludeText, newSock, warning
    };

    const Container = ({ queries, heading, type='other', lastMessages={} }) => <div className={s.main_flex_left}>
        <PrimaryHeading text={heading} className={s.main_heading} />
        <NotificationsJSX {...props} queries={queries} type={type} lastMessages={lastMessages} />
    </div>;

    return <>
        <Warning text={warning} className={s.main_flex_warning} />
        {!isLeft ? <Container heading='New Queries' queries={queries.other} /> :
        <Container heading='Answered Queries' queries={queries.my} type='my' lastMessages={lastMessages} />}
    </>
};

const matchStateToProps = (state = {}) => ({ joinChatReducer: state.joinChatReducer });
export default connect(matchStateToProps)(Notification); 