import React from 'react';
import Window from '../../../widgets/Window/Window';

import s from './Wait.module.scss';

const Wait = () => {
    return <main className={s.main}>
        <Window 
            initiator='Click on any chat from left or right to start chatting'
            isAlive canLoad className={s.main_window} hideLoading
        />
    </main>
};

export default Wait;