import { useEffect, useState } from 'react';
import {
    useHistory, useLocation
} from 'react-router-dom';
import Sock from '../sockets';

export const useLocationState = (
    key, path, 
    validator = () => true
) => {
    const { state, hash } = useLocation();
    const { replace } = useHistory();
    const [pageState, setPageState] = useState(null);
    useEffect(() => {
        if(state && state[key] && validator(state[key], hash, state)){
            setPageState(state[key]);
        }else{
            replace(path);
        };
        return () => setPageState(null);
    }, [state]);

    return [pageState, hash ? hash.substring(1) : null, state];
};

export const useStoreCleanupEffect = (callback, deps, [dispatch, types]) => {
    useEffect(() => {
        callback();
        // return () => {
        //     types.forEach(type => dispatch({ type, payload: null }));
        // };
    }, deps);
};

export const useSocket = () => {
    
    const [newSock, setNewSock] = useState(null);
    useEffect(() => {
        setNewSock(new Sock());
        return () => {
            if(Boolean(newSock)) newSock.disconnect();
        };
    }, []);

    return newSock;
};

export const useMessage = (chatId) => {
    
    const [newSock, setNewSock] = useState(null);
    const [isAlive, setIsAlive] = useState(true);
    const [canReview, setCanReview] = useState(false);
    const [messages, setMessages] = useState([]);
    useEffect(() => {
        if(!chatId) return;
        setNewSock(new Sock());
        setIsAlive(true);
    }, [chatId]);
    useEffect(() => {
        if(!newSock) return;
        newSock.emitEvent('ALL_MESSAGES', { parentChat: chatId });
        newSock.listenEvent(chatId + '_CHAT_FINISHED', ({ isReviewed, short }) => {
            setIsAlive(false);
            setCanReview(!isReviewed);
            if (short) newSock.emitEvent('SEARCH_EXECUTIVES', { id: null, executiveId: null });
        });
        newSock.listenEvent(`${chatId}_ALL_MESSAGES`, m => {
            // newSock.emitEvent('SEARCH_EXECUTIVES', { id: chatId })
            setMessages(m);
        });
        newSock.listenEvent(`${chatId}_MESSAGE_RECEIVED`, message => setMessages(m => {
            return [...m, message];
        }));
        return () => newSock.disconnect();
    }, [newSock, chatId]);

    return [messages, isAlive, canReview];
};

export const useTyping = (message, by, chatId) => {

    const DEFAULT_STATUS = { 'user': false, 'executive': false };
    const newSock = useSocket();
    const [status, setStatus] = useState(DEFAULT_STATUS);
    useEffect(() => {
        if(!newSock) return;
        newSock.listenEvent(chatId + '_TYPING_STATUS', s => setStatus(prev => ({ ...prev, ...s })));
        return () => newSock.disconnect();
    }, [newSock, by, chatId]);

    return status;
};

export const useKeyEffect = (action, key = 'Enter') => {
    useEffect(() => {
        const pickEvent = e => {
            if(e.ctrlKey && e.key === key){
                action();
            };
        };
        window.addEventListener('keydown', pickEvent);
        return () => window.removeEventListener('keydown', pickEvent);
    });
};

export const useLastMessage = (chats) => {
    
    const [last, setLast] = useState({});
    const newSock = useSocket();
    useEffect(() => {
        if(!newSock || !chats) return;
        chats.forEach(chat => {
            newSock.listenEvent(`${chat.id}_LAST_MESSAGE`, ({ by, content, at, highlight }) => {
                setLast(l => {
                    return { ...l, [chat.id]: { by, content, at, highlight } }
                });
            });
        });
    }, [newSock, chats]);

    useEffect(() => {
        setLast(chats.reduce((acc, val) => {
            acc[val.id] = { content: '', by: '' };
            return acc;
        }, {}));
    }, [chats]);

    return [last, setLast];
};

export const useTypingEmitter = (newSock, message, by, chatId) => {
  
    const [changed, setChanged] = useState(!!message);
    useEffect(() => setChanged(!!message), [message]);
    useEffect(() => {
        if(!newSock) return;
        if(changed){
            newSock.emitEvent('START_TYPING', { chatId, by });
        }else{
            newSock.emitEvent('END_TYPING', { chatId, by });
        };
    }, [newSock, changed, by, chatId]);
};

export const warnLeaving = e => {
    if(!e) e = window.event;
    e.cancelBubble = true;
    e.returnValue = 'Leaving this page will terminate all your ongoing chats.';

    if (e.stopPropagation) {
        e.stopPropagation();
        e.preventDefault();
    };
};

export const getFirstN = (str, n = 10) => str.length > n ? `${str.slice(0, n)}...` : str;