import React from 'react';
import { connect } from 'react-redux';
import { ImHappy, ImSad } from 'react-icons/im';
import PrimaryHeading from '../Heading/Heading';
import { DARK_BOX_STYLES } from '../../styles';

import s from './Box.module.scss';

const Box = ({
    info = '',
    value = '',
    refer,
    tr,
    style,
    ...props
}) => {

    return <section 
            style={tr === 'dark' ? {...DARK_BOX_STYLES, ...(style || {})} : {...(style || {})}} 
            className={[s.main, props.className].join(' ')}
        >
        <span className={s.main_key}>{info}</span>
        <span className={s.main_value}>{value}</span>
    </section>
};

const matchStateToProps = (state = {}) => {
    const { themeReducer: tr } = state;
    return { tr };
};

export default connect(matchStateToProps)(Box);

export const Board = ({
    onSubmit
}) => {
    return <section className={s.board}>
        <PrimaryHeading 
            className={s.board_heading}
            text='Did this chat solve your issue?'
        />
        <div className={s.board_row}>
            <ImHappy onClick={() => onSubmit(true)} className={s.board_row_icon} />
            <ImSad onClick={() => onSubmit(false)} className={s.board_row_icon} />
        </div>
    </section>
}