import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { loginUser } from '../../../actions';
import Card from '../../../widgets/Card/Card';
import Form from '../../../widgets/Form/Form';
import { useHistory } from 'react-router-dom';
import s from './Login.module.scss';
import { useWarning } from '../../../widgets/Warning/Warning';

const DEFAULT_LOGIN = { name: '', password: '' };
const Login = ({
    dispatch,
    loginReducer
}) => {

    const { push } = useHistory();
    const [canSubmit, setCanSubmit] = useState(true);
    const [message, setMessage] = useWarning('');
    const [loginState, setLoginState] = useState(DEFAULT_LOGIN);
    const loginExecutive = () => {
        setCanSubmit(false);
        const { name, password } = loginState;
        dispatch(loginUser(name, password));
    };

    useEffect(() => {
        if(!loginReducer) return;
        setCanSubmit(true);
        const { success, message, ...user } = loginReducer;
        if(!success) return setMessage(message)
        push({ pathname: '/executiveChat', state: { user } });
        return () => dispatch({ type: 'LOGIN_USER', payload: null });
    }, [loginReducer]);

    return <main className={s.main}>
        <Card className={s.main_card}>
            <Form 
                heading='Executive Login'
                format={[
                    { 
                        type: 'text', uid: 'name', 
                        onChange: e => setLoginState(s => ({ ...s, name: e })), 
                        value: loginState.name, placeholder: 'Name' 
                    },
                    {  
                        type: 'password', uid: 'pass', 
                        onChange: e => setLoginState(s => ({ ...s, password: e })), 
                        value: loginState.password, placeholder: 'Password' 
                    },
                ]} cta='Login' onSubmit={loginExecutive}
                canSubmit={canSubmit} error={message}
            />
        </Card>
    </main>
};

const matchStateToProps = (state = {}) => {
    const { loginReducer } = state;
    return { loginReducer };
};

export default connect(matchStateToProps)(Login);