import React, { useEffect, useState } from 'react';
import { useLocationState, useMessage, useSocket, useTyping, useTypingEmitter } from '../../../hooks';
import Window from '../../../widgets/Window/Window';
import s from './ExecutiveChat.module.scss';
import PrimaryHeading from '../../../widgets/Heading/Heading';
import Notification from '../Notification/Notification';
import Warning, { useWarning } from '../../../widgets/Warning/Warning';
import Layout from '../Layout/Layout';
import Wait from '../../User/Wait/Wait';
import get from 'lodash/get';

const ExecutiveChat = () => {

    const newSock = useSocket();
    const [mess, setMess] = useWarning('');
    const [message, setMessage] = useState('');
    const [user, _, state] = useLocationState('user', '/', (_, __, state) => !!state);
    const [hash, setHash] = useState(get(state, 'chat.id', null));
    const typingStatus = useTyping(message, 'executive', hash);
    useTypingEmitter(newSock, message, 'executive', hash);
    const [allMessages, isAlive] = useMessage(hash);
    const sendMessage = (content, clientId) => {
        newSock.emitEvent('SEND_MESSAGE', { content, clientId, by: 'executive', parentChat: hash, createdAt: new Date()});
        newSock.emitEvent('LAST_MESSAGE', { parentChat: hash, content, by: 'Executive', at: new Date() });
        setMessage('');
    };
    useEffect(() => setHash(get(state, 'chat.id', null)), [state]);

    return <Layout 
        user={user} left={<Notification 
            user={user} orient='column' isLeft
            className={s.main_left_scroll}
        />} area={<div className={s.main_right}>
            <Warning text={mess} className={s.main_right_warning} />
            
            {!hash ? <Wait /> : <Window 
                messages={allMessages}
                message={message}
                setMessage={setMessage}
                onSend={sendMessage}
                isAlive={isAlive}
                type='executive'
                otherTyping={typingStatus.user}
                header={`#${get(state, 'chat.referenceId','')}`}
            />}
        </div>} right={<Notification 
            user={user} orient='column'
        />}
    />    
};

export default ExecutiveChat;