import axio from 'axios';

let BASE = "http://localhost:5000";
if(process.env.NODE_ENV !== 'development'){
    BASE = "/api";
};

const axios = axio.create({
    withCredentials: true
})

const plainRequest = ({
    url, type
}) => async dispatch => {
    try {
        const response = await fetch(url);
        const payload = await response.json();
        dispatch({
            type,
            payload: {
                ...payload,
                success: true
            }
        });
    } catch (err) {
        dispatch({
            type,
            payload: {
                success: false,
                ...err?.response?.data
            }
        });
    };
}

const simpleRequest = ({
    method = 'post', body = {},
    url = '', type = '',
}) => async dispatch => {  
    const config = {
        method,
        url: `${BASE}/${url}`,
        data: body,
        headers: { 
            'Content-Type': 'application/json'
        }
    };
    try {
        const response = await axios(config);
        const payload = response.data;
        dispatch({
            type,
            payload: {
                ...payload,
                success: true
            }
        });
    } catch (err) {
        dispatch({
            type,
            payload: {
                success: false,
                ...err?.response?.data
            }
        });
    };
};

const simpleFormRequest = ({
    body, ref, images, 
    url = '', slug, type = ''
}) => async dispatch => {
    const formData = new FormData();

    for(const key in body){
        formData.append(key, JSON.stringify(body[key]));
    }

    for(let i = 0; i < ref.current?.files?.length; i++){ 
        const imageName = '\\' + images.replace(slug, ref.current.files[i].name);
        formData.append('files', ref.current.files[i], imageName);
    };

    const config = {
        method: 'post',
        url: `${BASE}/${url}`,
        data: formData
    };

    try {
        const response = await axios(config);
        const payload = response.data;
        dispatch({
            type,
            payload: {
                ...payload,
                success: true
            }
        });
    } catch (err) {
        dispatch({
            type,
            payload: {
                success: false,
                ...err?.response?.data
            }
        });
    };
}

// ACCOUNTS
// CHECK CURRENT USER
export const checkStatus = type => simpleRequest({
    url: 'executives/current', type: type || 'CHECK_STATUS', method: 'get'
});

// LOGIN
export const loginUser = (username, password) => simpleRequest({
    url: 'executives/authenticate', type: 'LOGIN_USER',
    body: { username, password }
});


// CHATS
// CREATE CHAT
export const createChat = body => simpleRequest({
    url: 'chats/create', body, type: 'CREATE_CHAT'
});

// JOIN CHAT
export const joinChat = body => simpleRequest({
    url: 'chats/join', body, type: 'JOIN_CHAT'
});

// REVIEW CHAT
export const reviewChat = body => simpleRequest({
    url: 'chats/review', body, type:'FEED_REVIEW'
});
