import {
    useHistory, Route, Switch, 
    BrowserRouter as Router
} from 'react-router-dom';
import React, { useEffect } from 'react';
import Login from '../components/Executive/Login/Login';
import Start from '../components/User/Start/Start';
import Chat from '../components/User/Chat/Chat';
import ExecutiveChat from '../components/Executive/ExecutiveChat/ExecutiveChat';
import Wait from '../components/User/Wait/Wait';

const Routes = () => <Router>
    <Switch>
        <Route path='/' component={Login} exact />
        <Route path='/query' component={Start} />
        <Route path='/wait' component={Wait} />
        <Route path='/chat' component={Chat} />
        <Route path='/executiveChat' component={ExecutiveChat}/>
        <Route path='*' component={Almighty} />
    </Switch>
</Router>;

export default Routes;

const Almighty = () => {
    const { replace } = useHistory();
    useEffect(() => replace('/'), []);
    return <></>;
}