export const determineStatusStyles = status => {
    const styles = {
        placed: { backgroundColor: '#ffff0066', color: '#999900' },
        accepted: { backgroundColor: '#00ff0066', color: '#008800' },
        rejected: { backgroundColor: '#ff000066', color: '#dd0000' },
    }[status];
    return styles;
};